export const apiBaseURL = process.env.NODE_ENV === 'production' ? 'https://pia-project.azurewebsites.net' : 'https://pia-project.azurewebsites.net';
const appBaseURL = process.env.NODE_ENV === 'production' ? '/' : '/';
//#region external API calls
export async function searchFlights(params) {
    let reqData = {
        "originLocation": params.from,
        "destLocation": params.to,
        "departureDateTime": getDateFromString(params.departureDate) + "T00:00:00",
        "directFlights": params.directFlights,
        "adults": params.adults,
        "childs": params.childs,
        "infants": params.infants,
        "tripType": params.tripType,
        "cabinType": params.cabinType,
    };
    if(params.returnDate !== ""){
        reqData.returnDateTime = getDateFromString(params.returnDate) + "T00:00:00";
    }
    if(params.tripType === 3 && params.locs.length >0) {
        let cities = params.locs;
        let places = [];
        for(let l=0; l <  cities.length; l++){
            let city = cities[l];
            let rphNum = l +1;

            let depDateObj = getDateFromString(city.depDate) + "T00:00:00";
            places.push({
                "originLocation": city.from,
                "destLocation": city.to,
                "departureDateTime": getDateFromString(city.depDate) + "T00:00:00",
            });
        }
        reqData.multiCity = places;
    }
    //console.log("calling with following data");
    //console.log(JSON.stringify(reqData));
    console.log("api URL "+ apiBaseURL);
    let respData = await axios.post(apiBaseURL + "/api/Flights", reqData).then((response) => {
        // Handle success
        if (response.status === 200) {
            let d = response.data;
            if(d.result){
                return d.flights;
            }else {
                console.error(d.messages);
                console.error("itinerary count is zero");
                return [];
            }
        }
    }).catch((error) => {
        console.error(error);
        return []
    }).finally(() => {
        //Perform action in always
    });
    return respData;
}

export async function createPNR(params) {
    return await axios.post(`${apiBaseURL}/api/Flights/BookFlight`, params).then((response) => {
        // Handle success//Complete
        let isSuccessFull = false;
        if(response.status === 200) {
            if(response.data.CreatePassengerNameRecordRS.ApplicationResults.status.toLowerCase() === "complete") {
                isSuccessFull = true;
                console.log("PNR Created Successfully.");
            }
        }
        let detailMessage = "";
        if(!isSuccessFull){
            let errorsText = "";
            let waringsText = "";
            response.data.CreatePassengerNameRecordRS.ApplicationResults.Error.forEach(function (e) {
                e.SystemSpecificResults.forEach(function (ssr) {
                    ssr.Message.forEach(function (m) {
                        errorsText+= `<span>CODE: ${m.code} <br/> ${m.content}<span>`
                    });
                });
            });
            response.data.CreatePassengerNameRecordRS.ApplicationResults.Warning.forEach(function (e) {
                e.SystemSpecificResults.forEach(function (ssr) {
                    ssr.Message.forEach(function (m) {
                        waringsText+= `<span>CODE: ${m.code} <br/> ${m.content}<span>`
                    });
                });
            });

            detailMessage = `<span><b>Errors: </b>${errorsText}</span><span><b>Warnings: </b>${waringsText}</span>`;
            console.log("Failed to create PNR.");
            console.log("user data ");
            console.log(JSON.stringify(params));
        }
        console.log("PNR Response data");
        console.log(JSON.stringify(response.data));
        return {status: isSuccessFull, data: response.data, message: detailMessage};
    }).catch((error) => {
        console.error("Error details");
        console.error(JSON.stringify(error));
        return {status: false, data: null};
    }).finally(() => {
        //Perform action in always
    });
}

export async function getAirports(query) {
    let queryData = await axios
        .get(apiBaseURL + "/getlocation/" + query)
        .then((result) => {
            return result.data.data;
        })
        .catch((error) => {
            console.error("An error occurred.");
            console.error(error);
            return [];
        }).finally(() => {
            //Perform action in always
        });
    return queryData;
}

export async function getLocalFlightsData(params) {
    let respData = await axios.get("/meta-data/flights/lhe-to-lhr-one-way-9-feb-2021.json").then((response) => {
        // Handle success
        return processSearchFlights(response.data.groupedItineraryResponse);
    }).catch((error) => {
        console.error("An error occurred.");
        console.error(error);
        return [];
    }).finally(() => {
        //Perform action in always
    });
    return respData;
}

