export function isValidDDMMYYYYString(value) {
    if(logDateError) console.info("Date 14");
    try{
        let d = moment(value, "DD-MM-YYYY");
        return d._isValid;
    }catch (e) {
        return false;
    }
}

export function getAgeInDays(value) {
    if(logDateError) console.info("Date 15");
    try{
        let dOld = moment(value, "DD-MM-YYYY");
        if(dOld._isValid){
            return moment().diff(dOld,'days');
        }
    }catch (e) {
        return 0;
    }
}

export function getYyyyMmDd(dateObj) {
    if(logDateError) console.info("Date 16");
    if (dateObj === null || dateObj === undefined)
        return "";
    return moment(dateObj).format('YYYY-MM-DD');
}

function getYYYYMMDDFormat(dateOnly) {
    //i.e format is 11-Jan-2021 change it to 2021-01-11
    if(logDateError) console.info("Date 17");
    try {
        let parts = dateOnly.split("-");
        let year = parts[2];
        let month = parts[1].toLowerCase();
        let date = parts[0];
        let monthNum = "";
        switch (month) {
            case "jan":
                monthNum = "01";
                break;
            case "feb":
                monthNum = "02";
                break;
            case "mar":
                monthNum = "03";
                break;
            case "apr":
                monthNum = "04";
                break;
            case "may":
                monthNum = "05";
                break;
            case "jun":
                monthNum = "06";
                break;
            case "jul":
                monthNum = "07";
                break;
            case "aug":
                monthNum = "08";
                break;
            case "sep":
                monthNum = "09";
                break;
            case "oct":
                monthNum = "10";
                break;
            case "nov":
                monthNum = "11";
                break;
            case "dec":
                monthNum = "12";
                break;
        }
        return year + "-" + monthNum + "-" + date;
    } catch (e) {
        return dateOnly;
    }
}

