export function getCabinName(number) {
    let cabinValue = "Economy";
    switch (number) {
        case 'P':
        case 0:
            cabinValue = "Premium First";
            break;
        case 'F':
        case 1://
            cabinValue = "First";
            break;
        case 'J':
        case 2://
            cabinValue = "Premium Bus";
            break;
        case 'C':
        case 3://
            cabinValue = "Business";
            break;
        case 'S':
        case 4://
            cabinValue = "Premium Economy";
            break;
        case 'Y':
        case 5://Economy
            cabinValue = "Economy";
            break;
        default:
            cabinValue = number;
            break
    }
    return cabinValue;
}

export function getCabinValue(number) {
    let cabinValue = "Economy";
    switch (number) {
        case 6://Premium First
            cabinValue = "P";
            break;
        case 5://First
            cabinValue = "F";
            break;
        case 4://Premium Bus
            cabinValue = "J";
            break;
        case 3://Business
            cabinValue = "C";
            break;
        case 2://Premium Eco
            cabinValue = "S";
            break;
        case 1://Economy
            cabinValue = "Y";
            break;
        default:
            cabinValue = number;
            break
    }
    return cabinValue;
}

export function processSearchFlights(data) {
    let itineraries = [];
    try {
        let groups = data.itineraryGroups;
        for (let g = 0; g < groups.length; g++) {
            let dataItineraries = groups[g].itineraries;
            for (let d = 0; d < dataItineraries.length; d++) {
                let itineraryObj = dataItineraries[d];
                let obj = {};
                obj.id = `${g}${itineraryObj.id}`;
                obj.ref = `G-${g}_I-${itineraryObj.id}`;
                let legs = itineraryObj.legs;
                let legsData = [];
                //iterate legs data
                for (let l = 0; l < legs.length; l++) {
                    let legObj = {};
                    let legRef = legs[l].ref;

                    let legDescsObj = data.legDescs.filter(o => o.id === legRef);
                    if (legDescsObj.length > 0) {
                        let legDescObj = legDescsObj[0];
                        let schedules = legDescObj.schedules;
                        let schedulesData = [];

                        let flightDate = "";
                        //iterate schedules data
                        for (let s = 0; s < schedules.length; s++) {
                            let scheduleRef = schedules[s].ref;

                            let scheduleObj = data.scheduleDescs.filter(o => o.id === scheduleRef);
                            if (scheduleObj.length > 0) {
                                let scheduleDescObj = scheduleObj[0];
                                let depAirport = scheduleDescObj.departure.airport;
                                let legDescription = groups[g].groupDescription.legDescriptions.filter(o => o.departureLocation === depAirport);
                                if (legDescription.length > 0) {
                                    flightDate = legDescription[0].departureDate;
                                }
                                //console.log("Manipulating itinerary: " + obj.ref + " >>> leg id: "+ legRef+ " >>> schedule: " + scheduleRef);
                                if(schedules[s].departureDateAdjustment){
                                    let dateObj = flightDate;
                                    let adjustedDate = addDaysToDate(dateObj, schedules[s].departureDateAdjustment);
                                    flightDate = getYyyyMmDd(adjustedDate);
                                    //console.log("Manipulate date >>> BEFORE : "+ dateObj + " >>> AFTER : "+adjustedDate);
                                }
                                let scheduleDetails = {
                                    frequency: scheduleDescObj.frequency,
                                    stopCount: scheduleDescObj.stopCount,
                                    eTicketable: scheduleDescObj.eTicketable,
                                    totalMilesFlown: scheduleDescObj.totalMilesFlown,
                                    elapsedTime: scheduleDescObj.elapsedTime,

                                    departureAirport: scheduleDescObj.departure.airport,
                                    departureCity: scheduleDescObj.departure.city,
                                    departureCountry: scheduleDescObj.departure.country,
                                    departureDateTime: flightDate + "T" + scheduleDescObj.departure.time.substr(0, 8),
                                    departureAirportTerminal: scheduleDescObj.departure.terminal,

                                    arrivalAirport: scheduleDescObj.arrival.airport,
                                    arrivalCity: scheduleDescObj.arrival.city,
                                    arrivalCountry: scheduleDescObj.arrival.country,
                                    arrivalDateTime: flightDate + "T" + scheduleDescObj.arrival.time.substr(0, 8),
                                    arrivalAirportTerminal: scheduleDescObj.arrival.terminal,

                                    airlineCode: scheduleDescObj.carrier.operating,
                                    flightNumber: scheduleDescObj.carrier.operatingFlightNumber,
                                    equipmentCode: scheduleDescObj.carrier.equipment.code,
                                    equipmentTypeForFirstLeg: scheduleDescObj.carrier.equipment.typeForFirstLeg,
                                    equipmentTypeForLastLeg: scheduleDescObj.carrier.equipment.typeForLastLeg,
                                };
                                if (scheduleDescObj.arrival.dateAdjustment) {
                                    let dateTime = flightDate + "T" + scheduleDescObj.arrival.time.substr(0, 8);
                                    let adjustedDate = addDaysToDate(dateTime, scheduleDescObj.arrival.dateAdjustment);
                                    scheduleDetails.arrivalDateTime = adjustedDate;
                                    /*console.log("Update Arrival date for itinerary: " + obj.ref + " >>> leg id: "+ legRef+ " >>> schedule: " + scheduleRef );
                                    console.log("Before: " + dateTime);
                                    console.log("After: " + adjustedDate);*/
                                }
                                scheduleDetails.legId = legRef;
                                scheduleDetails.scheduleId = scheduleRef;
                                scheduleDetails.ref = `${obj.ref}_L-${legRef}_S-${scheduleRef}`;
                                schedulesData.push(scheduleDetails);
                            }//end if schedules length
                        }// end loop schedules
                        legObj.elapsedTime = legDescObj.elapsedTime;
                        legObj.schedules = schedulesData;
                        legsData.push(legObj);
                    }// end if legs length
                }//end loop legs
                obj.legs = legsData;

                //make itinerary summary
                let totalStopsObj = 0;
                let totalTimeObj = 0;
                let summaryObj = [];
                for (let di = 1; di <= legsData.length; di++) {
                    let dIndex = di - 1;
                    let ld = legsData[dIndex];
                    totalTimeObj += ld.elapsedTime;
                    let segmentCount = ld.schedules.length;
                    totalStopsObj += (segmentCount - 1);
                    let lastSeg = segmentCount - 1;

                    let stopNamesObj = [];
                    let depPointObj = {};
                    let arrPointObj = {};

                    depPointObj = ld.schedules[0];
                    arrPointObj = ld.schedules[lastSeg];
                    for (let si = 1; si < segmentCount; si++) {
                        stopNamesObj.push(ld.schedules[si].departureCity)
                    }

                    let legObj = {
                        elapsedTime: ld.elapsedTime,
                        depPoint: depPointObj,
                        arrPoint: arrPointObj,
                        stopNames: stopNamesObj,
                    };
                    summaryObj.push(legObj);
                }

                obj.summary = {
                    totalStops: totalStopsObj,
                    totalTime: totalTimeObj,
                    legs: summaryObj
                };

                let priceInfo = itineraryObj.pricingInformation[0];

                obj.totalFare = {
                    totalAmount: (priceInfo.fare.totalFare.totalPrice + amountToAdd),
                    taxAmount: priceInfo.fare.totalFare.totalTaxAmount,
                    currencyCode: priceInfo.fare.totalFare.currency,
                    baseFareAmount: (priceInfo.fare.totalFare.equivalentAmount + amountToAdd)
                };

                if (priceInfo.fare.governingCarriers !== undefined) {
                    obj.FlightCode = priceInfo.fare.governingCarriers.split(" ")[0];
                } else {
                    obj.FlightCode = "";
                }

                let passengerInfoObj = priceInfo.fare.passengerInfoList[0].passengerInfo;

                let bagWeightObj = "";
                let bagProvisionTypeObj = "";
                if (passengerInfoObj.baggageInformation !== undefined) {
                    let baggageRef = passengerInfoObj.baggageInformation[0].allowance.ref;
                    bagProvisionTypeObj = passengerInfoObj.baggageInformation[0].provisionType;

                    let bagDescs = data.baggageAllowanceDescs.filter(o => o.id === baggageRef);
                    if (bagDescs.length > 0) {
                        if (bagDescs[0].weight !== undefined) {
                            bagWeightObj = bagDescs[0].weight + " " + bagDescs[0].unit;
                        }
                    }
                }
                obj.baggage = {
                    bagWeight: bagWeightObj,
                    bagProvisionType: bagProvisionTypeObj
                };

                obj.pricingInformation = itineraryObj.pricingInformation;
                itineraries.push(obj);
            }//end loop outer most
        }//end groups loop
    } catch (e) {
        console.error("error in parsing data, check following for details.");
        console.error(e);
    }
    console.log("BFM Processed");
    console.log(JSON.stringify(itineraries));
    return itineraries;
}

export function getQueryParam(parameterName) {
    let queryString = window.location.search;
    let urlParams = new URLSearchParams(queryString);
    return urlParams.get(parameterName);
}

export function getRouteQuery() {
    let queryparams = window.location.search;
    queryparams = queryparams.substring(1);
    let rqObj = JSON.parse('{"' + decodeURI(queryparams.replace(/&/g, "\",\"").replace(/=/g,"\":\"")) + '"}');
    return rqObj;
}

export function getStopInfo(names) {
    if (names.length === 0)
        return "Non-Stop";
    return names.length + " Stop";
}

export function getFlightName(flightCode) {
    let airLines = this.$store.getters.getAirLines;
    let match = airLines.filter(o => o.Code.toLowerCase() === flightCode.trim().toLowerCase());
    if (match.length > 0) {
        return match[0].Name;
    }
    return flightCode;
}

export function getLayOver(schedules, index) {
    try {
        let count = schedules.length;
        if ((index + 1) === count) {
            return "";
        } else {
            let next = index + 1;
            let time = getDifference(schedules[next].departureDateTime, schedules[index].arrivalDateTime);
            let city = schedules[index].arrivalCity;
            return `<div class="info"><b>Layover</b>&nbsp;${time}&nbsp;|&nbsp;${city}</div>`;
        }
    } catch (e) {
        return "";
    }
}

export function getCountryName(countryCode) {
    let countries = this.$store.getters.getCountries;
    let match = countries.filter(o => o.value.toLowerCase() === countryCode.toLowerCase());
    if (match.length > 0) {
        return match[0].displayText;
    }
    return countryCode;
}

export function getUniqueId(prefix, outerIndex, innerIndex) {
    return prefix + '-' + (outerIndex + 1) + '-' + (innerIndex + 1);
}

export function getAirportName(airportCode) {
    let airportCodeLC = airportCode.toLowerCase();
    let arr = this.$store.getters.getAirport.filter(o => o.iata !== null);
    let airports = arr.filter(o => o.iata.toLowerCase() === airportCodeLC);
    if (airports.length > 0) {
        return airports[0].name;
    }
    return airportCode;
}

export function isObject(item) {
    if ((!!item) && (item.constructor === Object)) {
        return this.getItemData(item);
    } else if (Array.isArray(item)) {
        return this.getItemData(item);
    } else {
        return item;
    }
}

export function getItemData(item) {
    let queryString = Object.keys(item).map(key => `<span style="display: inline-block; border: 1px dashed #999; padding: 5px; margin-right:10px;"><b>${key}</b> : ${this.isObject(item[key])}</span>`).join('');
    return queryString;
}

export function getTripTypeName(id) {
    switch (id) {
        case 1:
            return "One Way";
        case 2:
            return "Return";
        case 3:
            return "Multicity";
        default:
            return id;
    }
}

export function inputDateValidator(value, event) {
    // do not format on deletion, this could leave the input in bad state
    // but allows user to delete the leading 0 if needed for some reason
    if (event.inputType !== 'deleteContentBackward') {
        let parts = value.masked.split('-');
        if (parts[0] > 31) {
            return `0${parts[0].charAt(1)}`;
        }
        if (parts[1] > 12) {
            return `${parts[0]}0${parts[1]}`;
        }
        var today = new Date();
        var year = today.getFullYear();
        if (parts[2] > year) {
            return `${parts[0]}${parts[1]}${year}`;
        }
        if(value.masked.length === 10){
            if(isValidDDMMYYYYString(value.masked) === false)
                return '';
        }
    }
}
