import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import * as Cookies from 'js-cookie';
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isLive: false,
        bookingFlightDetail: {},
        pnrObj: {},
        userSearch: {},
        airports: {},
        multiCitySelection: {},
        authToken: "",
        testToken: "",
        airLines: [],
        countries: [],
    },
    plugins: [createPersistedState({
        key: 'sabre-data'
    })],
    mutations: {
        isLive(state, boolen) {
            state.isLive = boolen;
        },
        setBookingFlightDetail(state, detail) {
            state.bookingFlightDetail = detail;
        },
        setPnrObj(state, pnrObj) {
            state.pnrObj = pnrObj;
        },
        setUserSearch(state, search) {
            state.userSearch = search;
        },
        setAirports(state,data) {
            state.airports = data;
        },
        setMultiCitySelection(state,data) {
            state.multiCitySelection = data;
        },
        setAuthToken(state,data) {
            state.authToken = data;
        },
        setTestToken(state,data) {
            state.testToken = data;
        },
        setAirLines(state,data) {
            state.airLines = data;
        },
        setCountries(state,data) {
            state.countries = data;
        }
    },
    actions: {


    },
    getters: {
        isLive: state => state.isLive,
        getBookingFlightDetail: state => state.bookingFlightDetail,
        getPnrObj: state => state.pnrObj,
        getUserSearch: state => state.userSearch,
        getAirport: state => state.airports,
        getMultiCitySelection: state => state.multiCitySelection,
        getAuthToken: state => state.authToken,
        getTestToken: state => state.testToken,
        getAirLines: state => state.airLines,
        getCountries: state => state.countries
    }
});
