<template>
    <div id="appWrapper" v-bind:class="domain">
        <main>
            <transition name="fade" mode="out-in">
                <router-view></router-view>
            </transition>
        </main>
    </div>
</template>
<script>
    import * as DAO from "./api/apiCalls";
    import store from "./store";

    export default {
        name: 'APP',
        components: {
        },
        data() {
            return {
                loading: true,
                domain: "",
            }
        },
        methods: {
            bindEvents() {
                if (typeof (BindCustomLibraryEvents) == "function") {
                    // eslint-disable-next-line no-undef
                    BindCustomLibraryEvents();
                }
            }
        },
        //life cycle hooks
        beforeCreate() {
            //console.log("beforeCreate called 1");
            DAO.getAirlines().then(response => {
                this.$store.commit('setAirLines', response.data);
            });
            DAO.getCountries().then(response => {
                this.$store.commit('setCountries', response.data);
            });
        },
        created() {
            //console.log("created called 2");
        },
        beforeMount() {
            //console.log("beforeMount called 3");
            let env = process.env.NODE_ENV === 'production';
            this.$store.commit('isLive', env);
            DAO.getAirportsData().then(response => {
                this.$store.commit('setAirports', response);
            });
            this.$store.commit('setAuthToken', "");
            this.$store.commit('setTestToken', "");
            if(env){
                //destroy store data
                this.$store.commit('setUserSearch', null);
                this.$store.commit('setBookingFlightDetail', null);
                this.$store.commit('setMultiCitySelection', null);
                this.$store.commit('setPnrObj', null);
            }
        },
        mounted() {
            //console.log("mounted called 4");
            let _this = this;
            setTimeout(function () {
                _this.loading = false;
            }, 1000);
            this.domain = location.hostname;
        },
        beforeUpdate() {
            //console.log("beforeUpdate called 5");
        },
        updated() {
            //console.log("updated called 6");
            this.bindEvents();
        },
        activated() {
            //console.log("activated called 7");
        },
        deactivated() {
            //console.log("deactivated called 8");
        },
        beforeUnmount() {
            //console.log("beforeUnmount called 9");
        },
        unmounted() {
            //console.log("unmounted called 10");
        },
        errorCaptured() {
            //console.log("errorCaptured called 11");
        },
        renderTracked() {
            //console.log("renderTracked called 12");
        },
        renderTriggered() {
            //console.log("renderTriggered called 13");
        },
    }
</script>
<style lang="scss">
    @import "scss/styles.scss";
</style>
