<template>
    <form class="search-form" autocomplete="off" :class="inputClass" v-if="fields" @submit.prevent="handleInput">
        <div class="form-row">
            <div class="col-sm-12">
                <div class="custom-control custom-radio custom-control-inline" v-for="(ft,ftIndex) in fields.TripTypes"
                     :key="ftIndex">
                    <input type="radio" v-bind:id="'rbTripType'+ftIndex" v-bind:value="ft.value"
