        </div>
        <div class="form-row no-padding-row" v-bind:class="tripClass">
            <div class="col-sm col-12 multi-city-fields">
                <!--field group 1-->
                <div class="row" v-if="multiCityCount >= 2">
                    <div class="col-sm col-12 search-column">
