                            <datepicker id="dateDeparture1" name="dateDeparture1"
                                        v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :required="true" :disabled-dates="departureMinDate1"
                                        v-model="inpDepartureDate1" @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY"></datepicker>
                        </div>
                    </div>
                </div>
                <!--field group 2-->
                <div class="row" v-if="multiCityCount >= 2">
                    <div class="col-sm col-12 search-column">
                        <label for="fromAirport2">{{ fields.fromCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="fromAirport2" v-model="fromSearch2" :options="optionsFrom2"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-12 search-column">
                        <label for="toAirport2">{{ fields.toCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="toAirport2" v-model="toSearch2" :options="optionsTo2"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-10">
                        <label for="dateDeparture2">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateDeparture2" name="dateDeparture2"
                                        v-bind:placeholder="fields.departure.placeholder"
