                    </div>
                </div>
                <!--field group 3-->
                <div class="row" v-if="multiCityCount >=3">
                    <div class="col-sm col-12 search-column">
                        <label for="fromAirport3">{{ fields.fromCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="fromAirport3" v-model="fromSearch3" :options="optionsFrom3"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-12 search-column">
                        <label for="toAirport3">{{ fields.toCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="toAirport3" v-model="toSearch3" :options="optionsTo3"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-10">
                        <label for="dateDeparture3">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateDeparture3" name="dateDeparture3"
                                        v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :required="optionsTo3.required"
                                        :disabled="waitingForDepDate2" :disabled-dates="departureMinDate3"
                                        :open-date="nextDate3" v-model="inpDepartureDate3"
                                        @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY"></datepicker>
                        </div>
                    </div>
                    <div class="col-sm col-2">
                        <button type="button" v-on:click="addNewSet('2')"><i class="fa fa-minus-circle"></i><span
                                class="sr-only">Remove this</span></button>
                    </div>
                </div>
                <!--field group 4-->
                <div class="row" v-if="multiCityCount >=4">
                    <div class="col-sm col-12 search-column">
                        <label for="fromAirport4">{{ fields.fromCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="fromAirport4" v-model="fromSearch4" :options="optionsFrom4"
                                           :onItemSelected="handleSelectedValue"
