                            <AirportSearch ref="toAirport4" v-model="toSearch4" :options="optionsTo4"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-10">
                        <label for="dateDeparture4">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateDeparture4" name="dateDeparture4"
                                        v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :required="optionsTo4.required"
                                        :disabled="waitingForDepDate3" :disabled-dates="departureMinDate4"
                                        :open-date="nextDate4" v-model="inpDepartureDate4"
                                        @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY"></datepicker>
                        </div>
                    </div>
                    <div class="col-sm col-2">
                        <button type="button" v-on:click="addNewSet('2')"><i class="fa fa-minus-circle"></i><span
                                class="sr-only">Remove this</span></button>
                    </div>
                </div>
                <!--field group 5-->
                <div class="row" v-if="multiCityCount >=5">
                    <div class="col-sm col-12 search-column">
                        <label for="fromAirport5">{{ fields.fromCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="fromAirport5" v-model="fromSearch5" :options="optionsFrom5"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-12 search-column">
                        <label for="toAirport5">{{ fields.toCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="toAirport5" v-model="toSearch5" :options="optionsTo5"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-10">
                        <label for="dateDeparture5">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateDeparture5" name="dateDeparture5"
                                        v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :required="optionsTo5.required"
                                        :disabled="waitingForDepDate4" :disabled-dates="departureMinDate5"
                                        :open-date="nextDate5" v-model="inpDepartureDate5"
                                        @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY"></datepicker>
                        </div>
                    </div>
                    <div class="col-sm col-2">
                        <button type="button" v-on:click="addNewSet('2')"><i class="fa fa-minus-circle"></i><span
                                class="sr-only">Remove this</span></button>
                    </div>
                </div>
                <!--field group 6-->
                <div class="row" v-if="multiCityCount >=6">
                    <div class="col-sm col-12 search-column">
                        <label for="fromAirport6">{{ fields.fromCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="fromAirport6" v-model="fromSearch6" :options="optionsFrom6"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-12 search-column">
                        <label for="toAirport6">{{ fields.toCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="toAirport6" v-model="toSearch6" :options="optionsTo6"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-10">
                        <label for="dateDeparture6">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateDeparture6" name="dateDeparture6"
                                        v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :required="optionsTo6.required"
                                        :disabled="waitingForDepDate5" :disabled-dates="departureMinDate6"
                                        :open-date="nextDate6" v-model="inpDepartureDate6"
                                        @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY"></datepicker>
                        </div>
                    </div>
                    <div class="col-sm col-2">
                        <button type="button" v-on:click="addNewSet('2')"><i class="fa fa-minus-circle"></i><span
                                class="sr-only">Remove this</span></button>
                    </div>
                </div>
                <!-- multi city fields end here-->
            </div>
            <div class="col-sm col-12 from-to-fields">
                <div class="row">
                    <div class="col-sm col-12 search-column">
                        <label for="fromAirport">{{ fields.fromCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="fromAirport" v-model="fromSearch" :options="optionsFrom"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-12 search-column">
                        <label for="toAirport">{{ fields.toCity.title }}</label>
                        <div class="form-item icon-field">
                            <i class="fa fa-map-marker"></i>
                            <AirportSearch ref="toAirport" v-model="toSearch" :options="optionsTo"
                                           :onItemSelected="handleSelectedValue"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="col-sm col-6 dp-column">
                        <label for="dateDeparture">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateDeparture" name="dateDeparture"
                                        v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :disabled-dates="departureMinDate" :required="true"
                                        v-model="inpDepartureDate" @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY"></datepicker>
                        </div>
                    </div>
                    <div class="col-sm col-6 dp-column">
                        <label for="dateReturn">{{ fields.retrun.title }}</label>
                        <div class="form-item date-picker icon-field">
                            <i class="fa fa-calendar"></i>
                            <datepicker id="dateReturn" name="dateReturn" v-bind:placeholder="fields.retrun.placeholder"
                                        :disabled="waitingForDepDate" :disabled-dates="returnMinDate"
                                        :open-date="nextDate"
                                        :required="isRoundTrip"
                                        v-model="inpReturnDate" @selected="dateSelectedFunction"
                                        input-class="form-control"
                                        :format="getDDMMMYYYY" ref="dateReturnObj"></datepicker>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-sm col-12 cabin-column">
                <label for="cabinType">{{ fields.cabinTypes.title }}</label>
                <div class="form-item">
                    <select class="form-control" id="cabinType" v-model="qp.cabinType">
                        <option v-for="(ct,ctIndex) in fields.cabinTypes.values" v-bind:value="ct.value"
                                :key="ctIndex">
                            {{ct.title}}
                        </option>
                    </select>
                </div>
            </div>
            <div class="col-sm col-4 sel-column">
                <label for="adults">{{ fields.adults.title }}</label>
                <div class="form-item">
                    <select class="form-control smaller" id="adults" v-model="qp.adults" v-on:change="updateHeads">
                        <option v-for="(aValue,aIndex) in fields.adults.values" v-bind:value="aValue" :key="aIndex">
                            {{aValue}}
                        </option>
                    </select>
                </div>
            </div>
            <div class="col-sm col-4 sel-column">
                <label for="childs">{{ fields.childs.title }}</label>
                <div class="form-item">
                    <select class="form-control smaller" id="childs" v-model="qp.childs">
                        <option v-for="(cValue,cIndex) in fields.childs.values" v-bind:value="cValue" :key="cIndex">
                            {{cValue}}
                        </option>
                    </select>
                </div>
            </div>
            <div class="col-sm col-4 sel-column">
                <label for="infants">{{ fields.infants.title }}</label>
                <div class="form-item">
                    <select class="form-control smaller" id="infants" v-model="qp.infants">
                        <option v-for="(iValue,iIndex) in fields.infants.values" v-bind:value="iValue" :key="iIndex">
                            {{iValue}}
                        </option>
                    </select>
                </div>
            </div>
            <div class="col-sm col-12">
                <input class="btn btn-primary" type="submit" value="Find">
            </div>
        </div>
    </form>
</template>
<script>
    import Datepicker from 'vuejs-datepicker'
    import AirportSearch from "./AirportSearch";
    import * as DAO from '../api/apiCalls.js';
    import {
        getDDMMMYYYY,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDateFromString,
        getFlightsQueryString,
        getFlightObject,
        getDateObjFromString,
        isLocalHost,
        getRouteQuery
    } from '../api/utils.js';

    export default ({
        name: "FlightSearch",
        components: {
            Datepicker,
            AirportSearch,
        },
        props: {
            onSubmitClick: {
                type: Function,
            },
            inputClass: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                inpTripType: "1",
                inpDepartureDate: '',
                inpReturnDate: '',
                waitingForDepDate: true,
                waitingForDepDate1: true,
                waitingForDepDate2: true,
                waitingForDepDate3: true,
                waitingForDepDate4: true,
                waitingForDepDate5: true,
                fields: false,
                optionsFrom: {
                    placeholder: "LHR",
                    id: "fromAirport",
                    required: true
                },
                optionsTo: {
                    placeholder: "LHE",
                    id: "toAirport",
                    required: true
                },
                departureMinDate: {
                    to: minusDaysToDate(new Date(), 1),
                },
                departureMinDate1: {
                    to: minusDaysToDate(new Date(), 1),
                },
                departureMinDate2: {
                    to: minusDaysToDate(new Date(), 1),
                },
                departureMinDate3: {
                    to: minusDaysToDate(new Date(), 1),
                },
                departureMinDate4: {
                    to: minusDaysToDate(new Date(), 1),
                },
                departureMinDate5: {
                    to: minusDaysToDate(new Date(), 1),
                },
                departureMinDate6: {
                    to: minusDaysToDate(new Date(), 1),
                },
                returnMinDate: {
                    to: minusDaysToDate(new Date(), 1),
                },
                nextDate: addDaysToDate(new Date(), 1),
                nextDate2: addDaysToDate(new Date(), 1),
                nextDate3: addDaysToDate(new Date(), 1),
                nextDate4: addDaysToDate(new Date(), 1),
                nextDate5: addDaysToDate(new Date(), 1),
                nextDate6: addDaysToDate(new Date(), 1),
                qp: {
                    from: '',
                    to: '',
                    departureDate: '',
                    returnDate: '',
                    directFlights: false,
                    adults: 1,
                    childs: 0,
                    infants: 0,
                    tripType: 1,
                    cabinType: 1,
                },
                toSearch: '',
                fromSearch: '',
                totalHeads: 0,
                multiCityCount: 0,
                showMcFields: 2,
                fromAirport1: '',
                toAirport1: '',
                toSearch1: '',
                fromSearch1: '',
                inpDepartureDate1: '',
                optionsFrom1: {
                    placeholder: "LHR",
                    id: "fromAirport1",
                    required: false
                },
                optionsTo1: {
                    placeholder: "LHE",
                    id: "toAirport1",
                    required: false
                },
                fromAirport2: '',
                toAirport2: '',
                toSearch2: '',
                fromSearch2: '',
                inpDepartureDate2: '',
                optionsFrom2: {
                    placeholder: "LHR",
                    id: "fromAirport2",
                    required: false,
                    readonly: true
                },
                optionsTo2: {
                    placeholder: "LHE",
                    id: "toAirport2",
                    required: false
                },
                fromAirport3: '',
                toAirport3: '',
                toSearch3: '',
                fromSearch3: '',
                inpDepartureDate3: '',
                optionsFrom3: {
                    placeholder: "LHR",
                    id: "fromAirport3",
                    required: false,
                    readonly: true
                },
                optionsTo3: {
                    placeholder: "LHE",
                    id: "toAirport3",
                    required: false
                },
                fromAirport4: '',
                toAirport4: '',
                toSearch4: '',
                fromSearch4: '',
                inpDepartureDate4: '',
                optionsFrom4: {
                    placeholder: "LHR",
                    id: "fromAirport4",
                    required: false,
                    readonly: true
                },
                optionsTo4: {
                    placeholder: "LHE",
                    id: "toAirport4",
                    required: false
                },
                fromAirport5: '',
                toAirport5: '',
                toSearch5: '',
                fromSearch5: '',
                inpDepartureDate5: '',
                optionsFrom5: {
                    placeholder: "LHR",
                    id: "fromAirport5",
                    required: false,
                    readonly: true
                },
                optionsTo5: {
                    placeholder: "LHE",
                    id: "toAirport5",
                    required: false
                },
                fromAirport6: '',
                toAirport6: '',
                toSearch6: '',
                fromSearch6: '',
                inpDepartureDate6: '',
                optionsFrom6: {
                    placeholder: "LHR",
                    id: "fromAirport6",
                    required: false,
                    readonly: true
                },
                optionsTo6: {
                    placeholder: "LHE",
                    id: "toAirport6",
                    required: false
                },
            }
        },
        computed: {
            isRoundTrip: function () {
                return this.qp.tripType.toString() === "2";
            },
            tripClass: function () {
                return (this.inpTripType === "3") ? "multi" : "";
            }
        },
        methods: {
            getDDMMMYYYY,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            async getAirports(query, exactMatch = false) {
                let queryLC = query.toLowerCase();
                let arr = this.$store.getters.getAirport.filter(o => o.iata !== null && o.name !== null);
                if (exactMatch) {
                    return arr.filter(o => o.iata.toLowerCase() === queryLC || o.name.toLowerCase() === queryLC).sort(o => o.name);
                }
                if(queryLC.length === 3){
                    let iataList = arr.filter(o => o.iata.toLowerCase() === queryLC);
                    let nameList = arr.filter(o => o.iata.toLowerCase() !== queryLC && o.name.toLowerCase().indexOf(queryLC) >= 0).sort((a, b) => (a.name > b.name) ? 1 : -1);
                    return  iataList.concat(nameList);
                }
                let result = arr.filter(o => o.iata.toLowerCase().indexOf(queryLC) >= 0 || o.name.toLowerCase().indexOf(queryLC) >= 0);
                return result.sort((a, b) => (a.name > b.name) ? 1 : -1);
            },
            /** use the following function to server from API **/
            async getAirportsFromApi(query) {
                const result = await DAO.getAirports(query);
                return result;
            },
            async onCountryInputChange(query) {
                if (query.trim().length <= 2) {
                    return null
                }
                const result = await this.getAirports(query);
                return result;
            },
            addNewSet(value) {
                if (value === '1' && this.multiCityCount < 6) {
                    this.multiCityCount += 1;
                } else if (value === '2') {
                    this.multiCityCount -= 1;
                }
                let mcCount = 0;
                if(this.inpTripType === "3"){
                    mcCount = this.multiCityCount;
                }
                if (mcCount >= 2) {
                    this.optionsFrom1.required = true;
                    this.optionsFrom2.required = true;
                    this.optionsTo1.required = true;
                    this.optionsTo2.required = true;
                }
                if (mcCount >= 3) {
                    this.fromAirport3 = this.toAirport2;
                    this.fromSearch3 = this.toSearch2;
                    this.optionsFrom3.required = true;
                    this.optionsTo3.required = true;
                }
                if (mcCount >= 4) {
                    this.fromAirport4 = this.toAirport3;
                    this.fromSearch4 = this.toSearch3;
                    this.optionsFrom4.required = true;
                    this.optionsTo4.required = true;
                }
                if (mcCount >= 5) {
                    this.fromAirport5 = this.toAirport4;
                    this.fromSearch5 = this.toSearch4;
                    this.optionsFrom5.required = true;
                    this.optionsTo5.required = true;
                }
                if (mcCount >= 6) {
                    this.fromAirport6 = this.toAirport5;
                    this.fromSearch6 = this.toSearch5;
                    this.optionsFrom6.required = true;
                    this.optionsTo6.required = true;
                }
            },
            handleSelectedValue(item, id) {
                switch (id) {
                    case "fromAirport":
                        this.qp.from = item.iata;
                        this.fromSearch = item.name;
                        break;
                    case "toAirport":
                        this.qp.to = item.iata;
                        this.toSearch = item.name;
                        break;
                    case "fromAirport1":
                        this.fromAirport1 = item.iata;
                        this.fromSearch1 = item.name;
                        break;
                    case "toAirport1":
                        this.toAirport1 = item.iata;
                        this.fromAirport2 = item.iata;
                        this.toSearch1 = item.name;
                        this.fromSearch2 = item.name;
                        break;
                    case "toAirport2":
                        this.toAirport2 = item.iata;
                        this.fromAirport3 = item.iata;
                        this.toSearch2 = item.name;
                        this.fromSearch3 = item.name;
                        break;
                    case "toAirport3":
                        this.toAirport3 = item.iata;
                        this.fromAirport4 = item.iata;
                        this.toSearch3 = item.name;
                        this.fromSearch4 = item.name;
                        break;
                    case "toAirport4":
                        this.toAirport4 = item.iata;
                        this.fromAirport5 = item.iata;
                        this.toSearch4 = item.name;
                        this.fromSearch5 = item.name;
                        break;
                    case "toAirport5":
                        this.toAirport5 = item.iata;
                        this.fromAirport6 = item.iata;
                        this.toSearch5 = item.name;
                        this.fromSearch6 = item.name;
                        break;
                    case "toAirport6":
                        this.toAirport6 = item.iata;
                        this.toSearch6 = item.name;
                        break;
                }
            },
            updateHeads() {
                let end = 9 - parseInt(this.qp.adults, 10);
                let childs = [0];
                for (let s = 1; s <= end; s++) {
                    childs.push(s);
                }
                this.fields.childs.values = childs;
            },
            dateSelectedFunction(dateSelected) {

            },
            handleInput(event) {
                event.preventDefault();
                if (this.qp.tripType === "3") {
                    let depCities = [];
                    let mcCount = this.multiCityCount;
                    if (this.toSearch1 !== '' && mcCount >= 2) {
                        depCities.push({
                            "from": this.fromAirport1,
                            "to": this.toAirport1,
                            "fromName": this.fromSearch1,
                            "toName": this.toSearch1,
                            "depDate": this.inpDepartureDate1,
                        });
                    }
                    if (this.toSearch2 !== '' && mcCount >= 2) {
                        depCities.push({
                            "from": this.fromAirport2,
                            "to": this.toAirport2,
                            "fromName": this.fromSearch2,
                            "toName": this.toSearch2,
                            "depDate": this.inpDepartureDate2,
                        });
                    }
                    if (this.toSearch3 !== '' && mcCount >= 3) {
                        depCities.push({
                            "from": this.fromAirport3,
                            "to": this.toAirport3,
                            "fromName": this.fromSearch3,
                            "toName": this.toSearch3,
                            "depDate": this.inpDepartureDate3,
                        });
                    }
                    if (this.toSearch4 !== '' && mcCount >= 4) {
                        depCities.push({
                            "from": this.fromAirport4,
                            "to": this.toAirport4,
                            "fromName": this.fromSearch4,
                            "toName": this.toSearch4,
                            "depDate": this.inpDepartureDate4,
                        });
                    }
                    if (this.toSearch5 !== '' && mcCount >= 5) {
                        depCities.push({
                            "from": this.fromAirport5,
                            "to": this.toAirport5,
                            "fromName": this.fromSearch5,
                            "toName": this.toSearch5,
                            "depDate": this.inpDepartureDate5,
                        });
                    }
                    if (this.toSearch6 !== '' && mcCount >= 6) {
                        depCities.push({
                            "from": this.fromAirport6,
                            "to": this.toAirport6,
                            "fromName": this.fromSearch6,
                            "toName": this.toSearch6,
                            "depDate": this.inpDepartureDate6,
                        });
                    }
                    this.$store.commit('setMultiCitySelection', depCities);
                }
                let url = getFlightsQueryString(this.qp, '/flight.html');
                if (this.$route.path !== "/flight" || window.location.pathname != "/flight.html") {
                    window.location.href = url;
                } else {
                    this.$router.replace(url).catch(() => {});
                    this.$emit('onSubmitClick');
                }
            },
            async setFieldValues() {
                let querySting = getRouteQuery();
                this.qp = getFlightObject(querySting);
                if (Object.keys(querySting).length > 0) {
                    if (this.qp.departureDate !== '') {
                        this.inpDepartureDate = getDateObjFromString(this.qp.departureDate);
                    }
                    if (this.qp.returnDate !== '') {
                        this.inpReturnDate = getDateObjFromString(this.qp.returnDate);
                    }
                    if (this.qp.from !== '') {
                        const result = await this.getAirports(this.qp.from, true);
                        this.handleSelectedValue(result[0], "fromAirport");
                    }
                    if (this.qp.to !== '') {
                        const result = await this.getAirports(this.qp.to, true);
                        this.handleSelectedValue(result[0], "toAirport");
                    }

                    this.inpTripType = this.qp.tripType.toString();
                    if (querySting.tripType === "3") {
                        let locs = this.$store.getters.getMultiCitySelection;
                        let locsCount = locs.length;
                        if (locsCount > 0) {
                            if (locsCount >= 2) {
                                this.fromAirport1 = locs[0].from;
                                this.fromSearch1 = locs[0].fromName;
                                this.toAirport1 = locs[0].to;
                                this.toSearch1 = locs[0].toName;
                                this.inpDepartureDate1 = getDateObjFromString(locs[0].depDate);
                                this.fromAirport2 = locs[1].from;
                                this.fromSearch2 = locs[1].fromName;
                                this.toAirport2 = locs[1].to;
                                this.toSearch2 = locs[1].toName;
                                this.inpDepartureDate2 = getDateObjFromString(locs[1].depDate);
                            }
                            if (locsCount >= 3) {
                                this.fromAirport3 = locs[2].from;
                                this.fromSearch3 = locs[2].fromName;
                                this.toAirport3 = locs[2].to;
                                this.toSearch3 = locs[2].toName;
                                this.inpDepartureDate3 = getDateObjFromString(locs[2].depDate);
                            }
                            if (locsCount >= 4) {
                                this.fromAirport4 = locs[3].from;
                                this.fromSearch4 = locs[3].fromName;
                                this.toAirport4 = locs[3].to;
                                this.toSearch4 = locs[3].toName;
                                this.inpDepartureDate4 = getDateObjFromString(locs[3].depDate);
                            }
                            if (locsCount >= 5) {
                                this.fromAirport5 = locs[4].from;
                                this.fromSearch5 = locs[4].fromName;
                                this.toAirport5 = locs[4].to;
                                this.toSearch5 = locs[4].toName;
                                this.inpDepartureDate5 = getDateObjFromString(locs[4].depDate);
                            }
                            if (locsCount >= 6) {
                                this.fromAirport6 = locs[5].from;
                                this.fromSearch6 = locs[5].fromName;
                                this.toAirport6 = locs[5].to;
                                this.toSearch6 = locs[5].toName;
                                this.inpDepartureDate6 = getDateObjFromString(locs[5].depDate);
                            }
                            this.showMcFields = locs.length;
                        }
                    }
                }
                if(window.location.pathname === "/flight.html") {
                    let url = getFlightsQueryString(this.qp, '/flight');
                    this.$router.push(url);
                }
            },
        },
        beforeCreate() {
            DAO.getFieldsData().then(response => {
                this.fields = response.data;
                this.qp.directFlights = response.data.directFlights.value;
                this.updateHeads();
            });
        },
        mounted() {
            if(this.$route.path === "/flight" || window.location.pathname === "/flight.html") {
                this.setFieldValues();
            }
        },
        watch: {
            inpTripType: function (newValue) {
                if (newValue === "1" || newValue === "3") {
                    this.qp.returnDate = '';
                    this.inpReturnDate = null;
                    if(this.$refs.dateReturnObj !== undefined)
                        this.$refs.dateReturnObj.clearDate();
                }
                if (newValue === "3") {
                    this.multiCityCount = this.showMcFields;
                    if(this.$refs.fromAirport !== undefined){
                        this.$refs.fromAirport.extendedOptions.required = false;
                        this.$refs.toAirport.extendedOptions.required = false;
                    }
                    this.optionsFrom.required = false;
                    this.optionsTo.required = false;
                    let selector = ".flights-search-box-wrapper";
                    if(this.inputClass === ""){
                        selector = ".tabs-wrapper"
                    }
                    // eslint-disable-next-line no-undef
                    scrollToListing(selector);
                    this.addNewSet("3");
                } else {
                    this.multiCityCount = 0;
                    if(this.$refs.fromAirport !== undefined){
                        this.$refs.fromAirport.extendedOptions.required = true;
                        this.$refs.toAirport.extendedOptions.required = true;
                    }
                    this.optionsFrom.required = true;
                    this.optionsTo.required = true;
                }
                this.qp.tripType = newValue;
            },
            inpDepartureDate: function (newValue) {
                if (newValue !== null) {
                    this.waitingForDepDate = false;
                    this.qp.departureDate = getDDMMMYYYY(newValue);
                    this.returnMinDate.to = addDaysToDate(newValue, 1);
                    this.nextDate = addDaysToDate(newValue, 1);
                }
            },
            inpDepartureDate1: function (newValue) {
                if (newValue !== null) {
                    this.waitingForDepDate1 = false;
                    this.nextDate2 = addDaysToDate(newValue, 1);
                    this.departureMinDate2.to = newValue;//addDaysToDate(newValue, 1);
                }
            },
            inpDepartureDate2: function (newValue) {
                if (newValue !== null) {
                    this.waitingForDepDate2 = false;
                    this.nextDate3 = addDaysToDate(newValue, 1);
                    this.departureMinDate3.to = newValue;//addDaysToDate(newValue, 1);
                }
            },
            inpDepartureDate3: function (newValue) {
                if (newValue !== null) {
                    this.waitingForDepDate3 = false;
                    this.nextDate4 = addDaysToDate(newValue, 1);
                    this.departureMinDate4.to = newValue;//addDaysToDate(newValue, 1);
                }
            },
            inpDepartureDate4: function (newValue) {
                if (newValue !== null) {
                    this.waitingForDepDate4 = false;
                    this.nextDate5 = addDaysToDate(newValue, 1);
                    this.departureMinDate5.to = newValue;//addDaysToDate(newValue, 1);
                }
            },
            inpDepartureDate5: function (newValue) {
                if (newValue !== null) {
                    this.waitingForDepDate5 = false;
                    this.nextDate6 = addDaysToDate(newValue, 1);
                    this.departureMinDate6.to = newValue;//addDaysToDate(newValue, 1);
                }
            },
            inpReturnDate: function (newValue) {
                if (newValue != null) {
                    this.inpTripType = "2";
                    this.qp.returnDate = getDDMMMYYYY(newValue);
                }
            }
        },
    });
</script>

<style lang="scss" scoped>
    @import "../scss/FlightSearch.scss";
</style>
